import { Component, Input, OnInit } from '@angular/core';
import { SkeletonLoaderComponent } from 'src/app/shared/skeleton-loader/skeleton-loader.component';
import { FormatNumber } from 'src/app/util/formatNumber.util';

@Component({
	selector: 'app-resume',
	templateUrl: './resume.component.html',
	styleUrls: ['./resume.component.scss'],
	providers: [SkeletonLoaderComponent]
})
export class ResumeComponent implements OnInit {

	@Input() pedido: any;
	@Input() reserva: any;

	// Utils
	private formatNumber: FormatNumber = new FormatNumber();
	public messagePromo: string;

	constructor() {
	}

	ngOnInit() {
	}

	checkMessagePromo() {
		try {
			if (typeof this.reserva === 'object' && typeof this.reserva.MessagePromocaoVeiculo !== 'undefined' && this.reserva.MessagePromocaoVeiculo.indexOf(' 0 mÃªs') <= 1 && this.reserva.Periodos.length > 1) {
				this.messagePromo = this.reserva.MessagePromocaoVeiculo;
				return true;
			}
			if (typeof this.pedido === 'object' && typeof this.pedido.valores !== 'undefined' && this.pedido.valores.hasOwnProperty('promo') && this.pedido.valores.promo.hasOwnProperty('is_promo') && this.pedido.valores.promo.is_promo) {
				this.messagePromo = this.pedido.valores.promo.message;
				return true;
			}
			return false;
		} catch (error) {
			console.error(error)
			return false;
		}
	}

	getTotalMensal() {
		let total = null;

		try {

			if (typeof this.reserva === 'object') {

				try {

					if (typeof this.reserva.Periodos !== 'undefined' &&
						typeof this.reserva.Periodos[0] !== 'undefined') {
						total = this.reserva ? this.reserva.Periodos[0].valor_mensal_total : null;
					}

				} catch (error) {
					console.error('Error 1 - getTotalMensal : ' + error);
					return false;
				}

			} else if (typeof this.pedido === 'object') {

				try {

					if (typeof this.pedido.valores !== 'undefined' &&
						this.pedido.valores.hasOwnProperty('valor_mensal') &&
						this.pedido.valores.valor_mensal.hasOwnProperty('valor_mensal_total')) {
						total = this.pedido.valores.valor_mensal.valor_mensal_total ?
							this.pedido.valores.valor_mensal.valor_mensal_total : null;
					}

				} catch (error) {
					console.error('Error 2 - getTotalMensal : ' + error);
					return false;
				}

			}

			return total ? this.formatNumber.getBRLCurrency(Number(total)) : null;

		} catch (error) {

			console.error('Error 0 - getTotalMensal : ' + error);
			return false;

		}


	}

}
