import { Component, Input, OnInit } from '@angular/core';
import { DeviceService } from 'src/app/core/services/device.service';
import { BtnRedirectAccountComponent } from '../btn-redirect-account/btn-redirect-account.component';

@Component({
  selector: 'app-termos',
  templateUrl: './termos.component.html',
  styleUrls: ['./termos.component.scss'],
  providers: [BtnRedirectAccountComponent]
})
export class TermosComponent implements OnInit {

  isMobile: boolean;

  @Input()
  public enableButton? : boolean = false;

  constructor(private device: DeviceService) { }

  ngOnInit() {
    this.isMobile = this.device.isMobile();
  }

}
