import { Component, OnInit, OnDestroy } from '@angular/core';
import { OrderService } from 'src/app/core/services/order.service';
import { MainSharedService } from 'src/app/util/shared.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { AlertMessageComponent } from 'src/app/shared/alert-message/alert-message.component';
import { SubSink } from 'subsink';
import { CheckoutService } from 'src/app/core/services/checkout.service';
import { CheckLoginService } from 'src/app/core/services/checklogin.service';
import { GtmService } from 'src/app/core/services/gtm.service';
import { SeoService } from 'src/app/core/services/header/seo.service';

@Component({
  selector: 'app-confirm',
  templateUrl: './confirm.component.html',
  styleUrls: ['./confirm.component.scss'],
  providers: [AlertMessageComponent]
})
export class ConfirmComponent implements OnInit, OnDestroy {

  public orderId: string;
  public pedido: any;
  public reserva: any;
  public user: any;
  public emailPayer: string;

  public loadingOrder: boolean = true;
  public onDemand: boolean;
  public isOrderDetails: boolean = false;
  public isSeller: boolean = localStorage.getItem('choosenView') === 'seller';
  public enableButton: boolean;
  public enableRefreshStatus: boolean = false;
  public userLogged: number = 0;

  public enableWarning: boolean = false;
  public enableWarningByCnh: boolean = false;
  public enableWarningBySelfie: boolean = false;
  public warningTitle: string = 'Tudo certo!';
  public warningMessage: string = 'Agora, vocÃª pode acompanhar o status do seu pedido atravÃ©s da Ã¡rea do cliente.';

  private sub = new SubSink();

  constructor(
    private titleS: Title,
    private router: Router,
    private route: ActivatedRoute,
    private shared: MainSharedService,
    private gtm: GtmService,
    private orderService: OrderService,
    private alertMsg: AlertMessageComponent,
    private checkout: CheckoutService,
    private checkLoginService: CheckLoginService,
    private seoService: SeoService
  ) { }

  ngOnInit() {
    this.checkLoginService.check()
    this.titleS.setTitle('Zero Km - Pedido');
    this.seoService.createLinkForCanonicalURL();
    this.gtm.addPageGTM('Pageview ZeroKM', this.router.url, 'Zero Km - Pedido');
    //Verificar se o pageview relacionado vai ser quando entrar na pÃ¡gina de confirm ou somente quando o pagamento for aprovado!
    this.gtm.addPageGTM('TransaÃ§Ã£o ZeroKM', this.router.url, 'Zero Km - Pedido');
    this.user = this.shared.user;
    this.emailPayer = this.shared.user.email;
    this.shared.user && this.shared.user.PessoaID ? this.userLogged = this.shared.user.PessoaID : false
    this.loadPageConfigurations();
  }

  checkCreditAnalysis() {
    const defaultMsg = 'NÃ£o foi possÃ­vel realizar a anÃ¡lise do seu pedido no momento.';

    this.sub.sink = this.checkout.getCreditAnalysis(this.orderId).subscribe(res => {
      // VerificaÃ§Ã£o de mensagens de anÃ¡lise de crÃ©dito
      if (res && res.data) {
        let msg, title = ''
        switch (res.data) {
          case 'Aprovado':
            this.shared.setCounterLoading(false);
            this.verifyContract();
            break;
          case 'Analise Reprovada':
            this.shared.setCounterLoading(false);
            title = 'Desculpe!'
            msg = 'Infelizmente vocÃª nÃ£o passou pela anÃ¡lise de crÃ©dito.'
            this.setAlert(title, msg)
            break;
          case 'Em anÃ¡lise':
            this.shared.setCounterLoading(false);
            if (this.pedido.status.id === 12) {
              title = 'Aguarde!'
              msg = 'Seu pedido estÃ¡ passando por anÃ¡lise de crÃ©dito.'
              this.setAlert(title, msg)
            }
            break;
          case 'Processando pagamento':
            this.shared.setCounterLoading(false);
            title = 'Aguarde!'
            msg = 'Seu pagamento estÃ¡ sendo processado.'
            this.setAlert(title, msg)
            break;

          default:
            this.shared.setCounterLoading(false);
            title = 'AtenÃ§Ã£o!'
            this.setAlert(title, defaultMsg)
            break;
        }
      }
    }, ex => {
      this.shared.setCounterLoading(false);
      this.alertMsg.openDialog({
        tipo: 'message',
        titulo: 'AtenÃ§Ã£o!',
        mensagem: defaultMsg
      }, 'message', true);
      // this.checkUserToGetOrder();
    });
  }

  setAlert(title, msg) {
    this.alertMsg.openDialog({
      tipo: 'message',
      titulo: title,
      mensagem: msg
    }, 'message', true).then(dialogRef => {});
  }

  loadPageConfigurations() {
    const urlPath = this.router.url.split('?')[0];
    this.onDemand = this.shared.isOnDemand;
    this.orderId = this.route.snapshot.params.orderId;
    this.isOrderDetails = urlPath.includes('minha-conta/pedido') || urlPath.includes('assinatura/sucesso') ;
    if (!this.isOrderDetails && !this.isSeller) {
      this.checkCreditAnalysis();
      return;
    }
    this.checkUserToGetOrder();
  }

  checkWarning() {
    this.enableWarning = this.enableWarningBySelfie && this.enableWarningByCnh;
  }

  get enableDocuments() {
    return this.pedido && this.pedido.status && this.pedido.status.id !== 1;
  }

  verifyContract() {
    const data = {
      "PedidoFrotaID": parseInt(this.orderId)
    };
    this.sub.sink = this.checkout.getNewContract(data).subscribe(res => {
      if (res && res.success) {
        this.shared.setCounterLoading(false);
        this.shared.setLoadingAtivo(false);
        this.checkUserToGetOrder();
        return;
      }
      this.shared.setCounterLoading(false);
      this.checkUserToGetOrder();
    }, ex => {
      this.shared.setCounterLoading(false);
      this.alertMsg.openDialog({
        tipo: 'message',
        titulo: 'AtenÃ§Ã£o!',
        mensagem: 'NÃ£o foi possÃ­vel realizar a anÃ¡lise do seu pedido no momento.'
      }, 'message', true);
      // this.checkUserToGetOrder();
    });
  }

  checkUserToGetOrder(ignoreAnalysis = false) {
    // this.isSeller ? this.getSellerOrder() : this.getOrder(ignoreAnalysis);
    this.isSeller ? this.getOrder(true) : this.getOrder(ignoreAnalysis);
  }

  getOrder(ignoreAnalysis = false) {
    this.sub.sink = this.orderService.getOrder(this.orderId).subscribe(data => {
      // data.pedido && data.pedido.PedidoFrotaStatusID !== (1 || 3 || 5 || 7 || 9|| 10 || 12) ? this.enableRefreshStatus = true : this.enableRefreshStatus = false
      if (!data) {
        this.shared.setCounterLoading(false);
        this.alertMsg.openDialog({
          tipo: 'message',
          titulo: 'Algo deu errado!',
          mensagem: 'NÃ£o foi possÃ­vel carregar as informaÃ§Ãµes do seu pedido. Tente novamente mais tarde.'
        }, 'message', true);
        this.router.navigate(['/minha-conta']);
        return void (0);
      }
      data.pedido && data.pedido.PedidoFrotaStatusID !== (3 || 5 || 7 || 9|| 10) ? this.enableRefreshStatus = true : this.enableRefreshStatus = false
      this.pedido = data.pedido;
      this.reserva = data.reserva;
      this.shared.setCounterLoading(false);
      this.shared.setLoadingAtivo(false);
      this.loadingOrder = false;
      if (data.pedido.PedidoFrotaStatusID === 3 && !localStorage.getItem('approvedStatus')) {
        this.alertMsg.openDialog({
          icon: 'ok-icone.svg',
          tipo: 'message',
          titulo: 'Aprovado!',
          mensagem: 'Agora Ã© sÃ³ esperar um pouquinho que nosso time entrarÃ¡ em contato para agendar a entrega do seu veÃ­culo.'
        }, 'message', true).then(dialogRef => {
          this.sub.sink = dialogRef.afterClosed().subscribe(() => {
            localStorage.setItem('approvedStatus', '1-'+data.pedido.ReservaID)
          })
        })
      }
      if (!(!this.reserva || (!this.reserva.length && this.reserva.length === 0)) && !ignoreAnalysis && this.pedido.PedidoFrotaStatusID !== (3 || 5 || 7 || 9 || 10)) {
      // if (!(!this.reserva || (!this.reserva.length && this.reserva.length === 0)) && !ignoreAnalysis && this.pedido.PedidoFrotaStatusID !== (1 || 3 || 5 || 7 || 9 || 10 ||12)) {
          this.checkCreditAnalysis();
      }
    }, ex => {
      this.shared.setCounterLoading(false);
      this.alertMsg.openDialog({
        tipo: 'message',
        titulo: 'Algo deu errado!',
        mensagem: 'NÃ£o foi possÃ­vel carregar as informaÃ§Ãµes do seu pedido. Tente novamente mais tarde.'
      }, 'message', true);
      this.router.navigate(['/minha-conta']);
    });
  }

  getSellerOrder() {
    this.sub.sink = this.orderService.getSellerOrder(this.orderId).subscribe(data => {
      this.pedido = data;
      this.shared.setCounterLoading(false);
    }, ex => {
      this.shared.setCounterLoading(false);
      this.alertMsg.openDialog({
        tipo: 'message',
        titulo: 'Algo deu errado!',
        mensagem: 'NÃ£o foi possÃ­vel carregar as informaÃ§Ãµes do seu pedido. Tente novamente mais tarde.'
      }, 'message', true);
      this.router.navigate(['/minha-conta']);
    });
  }

  selfieUploaded() {
    this.enableWarningBySelfie = true;
    this.enableButton = true;
    this.checkWarning();
  }

  cnhUploaded() {
    this.enableWarningByCnh = true;
    this.enableButton = true;
    this.checkWarning();
  }

  ngOnDestroy() {
    this.shared.removeVehicleDetails();
    this.sub.unsubscribe();
  }
}
